export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			VerticalNavTop: 0,
			list: [],
			TabCur: 0,
			MainCur: 0,
			load: true
		}
	},
	computed: {},
	created() {
		let that = this
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		let list = [{}]
		for (let i = 0; i < 26; i++) {
			list[i] = {};
			list[i].name = String.fromCharCode(65 + i);
			list[i].id = i
		}
		that.list = list
	},
	mounted() {
		let that = this
		uni.hideLoading()
	},
	methods: {
		tabSelect(e) {
			let that = this
			that.TabCur = e.currentTarget.dataset.id
			that.MainCur = e.currentTarget.dataset.id
			that.VerticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {
			let that = this;
			let list = that.list;
			let tabHeight = 0;
			if (that.load) {
				for (let i = 0; i < list.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + list[i].id);
					view.fields({
						size: true
					}, data => {
						console.log(data)
						list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						list[i].bottom = tabHeight;
					}).exec();
				}
				that.load = false
				that.list = list
			}
			let scrollTop = e.detail.scrollTop + 20;
			for (let i = 0; i < list.length; i++) {
				if (scrollTop > list[i].top && scrollTop < list[i].bottom) {
					that.VerticalNavTop = (list[i].id - 1) * 50
					that.TabCur = list[i].id
					return false
				}
			}
		}
	}
}
